// Componente para mostrar un nÃºmero de posts determinado
import React, { useEffect } from 'react';
import Card from '../Card/Card';
import { useTranslation } from 'react-i18next';
import { Observer, GLOBAL_EVENT } from '../../Observer';

import './BlogList.scss';

const BlogList = ({ posts, postsInIndexPage }) => {
  const { i18n } = useTranslation();
  const posts_lang = posts.filter((edge, i) => {
    return (edge.node.fields.slug.startsWith(`/${i18n.language}/`));
  });

  useEffect(() => {
    Observer.emit(GLOBAL_EVENT.INDEX_POSTS_LOADED);
  })

  return (
    <div className="text-center mt-3 blogList container">
      {
        posts_lang.map((post, i) => {
          const node = post.node;
          const excerpt = (node.frontmatter.description !== null) ? node.frontmatter.description : node.excerpt;
          if (node.frontmatter.tags !== null && i < postsInIndexPage) {
            console.log('Cover @@@@@@@@@@: ', (node.frontmatter.cover && node.frontmatter.cover.publicURL));
            const tag = node.frontmatter.tags[0];
            return <Card className="mx-0 mx-sm-2 my-1 my-sm-3" key={i}
                slug={node.fields.slug}
                title={node.frontmatter.title}
                excerpt={excerpt}
                tag={tag}
                cover={node.frontmatter.cover && node.frontmatter.cover.publicURL}
              />;
          } else {
            return null;
          }
        })
      }
    </div>
  )
}

export default BlogList;
